.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

*{
 overflow-x: hidden;
}

.body *{
  box-sizing: border-box;
}

.header{
  width:100%;
  height:80px;
  padding:10px;
  background-color: aqua;
}

.banner{
  width:100%;
  height:400px;
  background-color: #282c34;
  background-image: url('https://i0.wp.com/teaser-trailer.com/wp-content/uploads/Polar-new-banner.jpg?ssl=1');
  position: relative;
}

.banner .detail {
  width:100%;
  position: absolute;
  height:200px;
  bottom:0;
  padding:20px;

}

.movie-list-wrapper{

  width: 100%;
  height: auto;
}

.movie-list-wrapper .movie-heading{
  width: 100%;
  height: 80px;
  text-align: center;
  font-size: 60px;
  padding:10px;

}

.movie-list-wrapper .movie-list{
  width:100%;
  height:auto;
  padding:30px;
  display:grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: auto;
  grid-gap : 30px;

}

.movie-list-wrapper .movie-list .movie-card{

  width:250px;
  height:300px;
  border: 5px;
  padding:20px;
  position: relative;
  box-shadow: 2px 2px 2px rgba(0 ,0, 0, 0.5 );
  border:1px solid #ccc;
  background-size: cover;
  /* flex-direction: column; */
}

.movie-list-wrapper .movie-list .movie-card h3{

color : white;

}
.movie-list-wrapper .movie-list .movie-card button{

  position: absolute;
  bottom:5%;
  left:20% ;
  padding:5px;
  background-color:#61dafb;
  width:150px;
  margin:auto;



}